import React from 'react'
import { Link, NavLink } from 'react-router-dom'



const MainNav = () => {
  return (
    <ul>
        <li> <NavLink to="/Sub01"></NavLink> </li>
        <li> <NavLink to="/Sub02"></NavLink> </li>
        <li> <NavLink to="/Sub03"></NavLink> </li>
        <li> <NavLink to="/Sub04"></NavLink> </li>
    </ul>
  )
}

export default MainNav