import React from "react";
import "../css/Subpage.scss";
import "../css/Sub02.scss";
import { useSelector } from "react-redux";

const Sub02 = () => {
    const { TRAFFIC, WEATHER } = useSelector(state => state)
    return (
        <section className="Subpage">
            <div className="Sh02"></div>
            <div className="inner sh">
                <h2></h2>
                <p>The Best Bleisure City,   .       ,   .</p>
                <div className="box">
                    <div className="left">
                        <h3>  </h3>
                        <ul>
                            {TRAFFIC.map((bus) => {
                                return (
                                    <li key={bus.id}>
                                        <span>{bus.taf}</span>
                                        {bus.des}
                                    </li>
                                );
                            })}
                        </ul>
                    </div>
                    <div className="right">
                        <h3>  </h3>
                        <ul>
                            {WEATHER.map((wea) => {
                                return (
                                    <li key={wea.id}>
                                        <span>{wea.taf}</span>
                                        {wea.des}
                                    </li>
                                );
                            })}
                        </ul>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Sub02;
