import React, { useEffect, useState } from "react";
import "../css/MainWeat.scss";

//  TMP 1  
// 	WSD		m/s	10
// 	SKY			4
//	POP		%	8

const MainWet = ({ weather }) => {
    const date = new Date().toLocaleDateString("en-GB").split("/").reverse().join("-");


    //   API 14    .       filter  4  .
    const nweat = weather?.filter((it) => it.category == "SKY" || it.category == "TMP" || it.category == "POP" || it.category == "WSD");

    // categoty       replace  .
    const TMP = nweat[0].category.replace("TMP", " ");
    const WSD = nweat[1].category.replace("WSD", "");
    const SKY = nweat[2].category.replace("SKY", " ");
    const POP = nweat[3].category.replace("POP", "");

    // const SKYValue = nweat[2].fcstValue.replace("1", "" || "3", " " || "4", "");
    
    // fcstValue replace  ""     "", ""          .
    

    // SKYValue    key,   value  SKYValue[nwet[2].fcstValue] fcstValue 1, 3, 4      value  .

    const SKYValue = {
        1: "",
        3: " ",
        4: ""
    }

    const [on, setOn] = useState(false);
    useEffect(() => {
        const scrollEvent = () => {
            let sct = window.scrollY;
            sct > 1100 ? setOn(true) : setOn(false);
        };
        window.addEventListener("scroll", scrollEvent);
        return () => {
            window.removeEventListener("scroll", scrollEvent);
        };
    }, []);

    return (
        <section className={`MainWeat csc ${on ? "on" : ""}`}>
            <div className="inner">
                <div className="mweat">
                    <h3>  </h3>
                    <span>{date}</span>
                    {console.log(nweat)}
                    <div className="wbox">
                        <div className="category">
                            <ul>
                                <li>{TMP}</li>
                                <li>
                                    <span>{nweat[0].fcstValue}</span> 
                                </li>
                            </ul>
                            <ul>
                                <li>{WSD}</li>
                                <li>
                                    <span>{nweat[1].fcstValue}</span> m/s
                                </li>
                            </ul>
                            <ul>
                                <li>{SKY}</li>
                                <li>
                                    <span>{SKYValue[nweat[2].fcstValue]}</span>
                                </li>
                            </ul>
                            <ul>
                                <li>{POP}</li>
                                <li>
                                    <span>{nweat[3].fcstValue}</span> %
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default MainWet;
